    import {mapState} from 'vuex';

    export default {
        name: "app-associated-link",
        data() {
            return {
                className: ''
            }
        },
        computed: {
            ...mapState({
                styleImg: state => state.mallConfig.__wxapp_img.diy
            })
        },
	    props: {
            arrowsSwitch: {
                type: Boolean,
	            default: function() {
	                return false;
	            }
            },
            background: {
                type: String,
	            default: function() {
	                return '#E11B1B';
	            }
            },
            color: {
                type: String,
	            default: function() {
	                return '#F4EBEB';
	            }
            },
            link: {
                type: Object,
	            default: function() {
	                return {
                        open_type: "tel",
                        params: [
	                        {
                                value: "111",
	                        }
                        ],
	                }
	            }
            },
            styleColor: {
                type: String,
	            default: function() {
	                return '#353535';
	            }
            },
            picSwitch: {
                type:Boolean,
	            default: function() {
	                return false;
	            }
            },
            picUrl: {
                type: String,
	            default: function() {
	                return '';
	            }
            },
		    position: {
                type: String,
	            default: function() {
	                return 'left';
	            }
		    },
		    title: {
                type: String,
			    default: function() {
			        return '';
			    }
		    },
		    styleNum: {
                type: String,
			    default: function() {
			        return '1';
			    }
		    },
		    fontSize: {
                type: String,
			    default: function() {
			        return '36';
			    }
		    }
	    },
        created() {
            let that = this;
            if(this.fontSize == '36') {
            	this.className = 'big-style'
            }else if(this.fontSize == '28') {
            	this.className = 'medium-style'
            }else {
            	this.className = 'small-style'
            }
        },
    }
